* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}
html {
  font-size: 62.5%;
  background-color: rgb(30, 27, 27);
}
.box {
  width: 90vw;
  margin: 0 auto;
}
.text-black {
  color: black;
}
.text-white {
  color: white;
}
.bg-white {
  background-color: white;
}
.bg-black {
  background-color: rgb(30, 27, 27);
}
body {
  width: 100vw;
  scroll-behavior: smooth;
  /* border:2px solid white; */
}
/* __________________________________________________________________________________________________

                                                    navbar css
__________________________________________________________________________________________________ */
.navbar-container {
  background-color: rgb(30, 27, 27);
  border-bottom: 1px;
  list-style-type: none;
  width: 100%;
  display: grid;
  grid-template-columns: 10rem 1fr 3fr 1fr 10rem;
  place-items: center;
  border-bottom: 1px solid rgb(44, 39, 39);
  position: sticky;
  left: 0;
  right: 0;
  top: 0;
  z-index: 4;
}
.logo-container {
  grid-column: 2/3;
  display: grid;
  place-items: center;
}
.navbar-logo {
  height: 180px;
  width: 180px;
}
.link-container {
  grid-column: 3/4;
}
.link-container ul {
  /* grid-template-columns: 2rem 1fr 1fr 1fr 1fr 2rem; */
  list-style-type: none;
  display: flex;
  justify-content: space-between;
}
.link-container ul li {
  /* grid-column: 2/3; */
  padding: 10px;
  /* margin:0px 10px; */
}

.link-container ul li a {
  font-size: 2rem;
  color: white;
  transition-duration: 200ms;
}
.link-container ul li a:hover {
  font-size: 2.2rem;
  color: rgb(22, 97, 247);
}
.authentication {
  grid-column: 4/5;
  padding: 20px;
  padding-left: 50px;
  display: flex;
  flex-wrap: wrap;
}
.btn {
  background-color: white;
  font-size: 1.6rem;
  padding: 8px;
  outline: 1px solid white;
  margin: 5px;
  cursor: pointer;
  border: none;
}

.btn:hover {
  background-color: rgba(255, 255, 255, 0.767);
}
.btn-primary {
  color: white;
  background-color: rgb(22, 97, 247);
}

.btn-primary:hover {
  background-color: rgba(22, 97, 247, 0.685);
}

/* __________________________________________________________________________________________________

                                          hero section css
__________________________________________________________________________________________________ */

.hero-section {
  /* display: grid; */
  /* grid-template-columns: 10rem 1.5fr 2fr 10rem; */
  color: white;
  place-items: center;
  /* border:1px solid yellow; */
}

.hero-section-content {
  /* grid-column: 2/3; */
  /* border:1px solid yellow; */
  padding: 30px;
  display:flex
}
.hero-section-left{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding:50px;
}
.hero-section-right{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.section-title {
  text-align: center;
  font-size: 6vw;
  margin: 8px;
}
.section-description {
  text-align: center;
  font-size: 2rem;
  margin-top: 15px;
  padding: 8px;
}
.section-image {
  grid-column: 3/4;
}
.hero-image {
  width: 100%;
}
.courses-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  color: black;
  background-color: white;
  justify-content: space-around;
  padding: 100px;
}
.course-card {
  margin: 20px 10px;
  padding: 10px;
  border: 1px solid black;
  border-radius: 2px;
  padding: 50px 10px;
  transition-duration: 200ms;
  width: calc(30vw);
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0);
}
.course-card:hover {
  transform: scale(110%);
}
.course-logo {
  font-size: 5rem;
  margin: 10px;
}
.course-title {
  margin: 10px;
  font-size: 3rem;
}
.course-description {
  font-size: 2rem;
  margin: 10px;
}

h1 {
  text-align: center;
  font-size: 6rem;
  background-color: white;
  padding: 20px 0px 0px 0px;
}

.m-bottom-150 {
  margin-bottom: 150px;
}
.m-top-100{
  margin-top: 100px;
}
/* 
                            Contact us Form CSS 
                                                                                          */

.contact-us-form-container {
  width: 70%;
  margin: 0px auto;
}

.form-content {
  display: grid;
  place-items: center;
  padding: 100px;
}
input {
  outline: none;
}
.form-content input[type="text"],
input[type="email"] {
  width: 100%;
  margin: 8px;
  padding: 2rem;
  font-size: 1.5rem;
}
/* footer css  */

.footer-container {
  border-top: 1px solid rgb(44, 39, 39);
  /* padding-top:100px; */
}
.footer-content {
  padding-top: 50px;
  padding-bottom: 5px;
  font-size: 1.3rem;
}
.social-media-links-container {
  position: fixed;
  bottom: 100px;
}
.social-media-links div {
  margin: 5px 0px;
  width: 150px;
  position: relative;
  left: -150px;
  transition: 200ms;
  display: flex;
  border-radius: 5px;
}
.social-media-links div a {
  text-align: right;
}
.social-media-links div a {
  margin: 1px auto;
  padding: 5px;
  color: white;
  text-decoration: none;
  font-size: 3rem;
  width: 150px;
}
.social-media-links div:hover {
  left: -100px;
}
.social-media-links .facebook {
  background-color: rgb(22, 97, 247);
}
.social-media-links .instagram {
  background-color: #e1306c;
}
.social-media-links .linkedin {
  background-color: #0077b5;
}
.social-media-links .twitter {
  background-color: rgb(22, 97, 247);
}
.social-media-links .github {
  background-color: #171515;
}
.social-media-links .whatsapp {
  background-color: rgb(37, 211, 102);
}
h4 {
  text-align: center;
  padding: 50px;
}
